                <!-- Secretaria -->
                <!-- <v-divider inset></v-divider> -->

                <v-subheader inset class="red--text" style="margin-left: 15em;"
                  >Secretaria</v-subheader
                >

                <v-list-item>
                  <v-list-item-avatar>
                    <v-icon class="grey darken-3" dark>
                      fas fa-file-alt
