                    </v-icon>
                  </v-list-item-avatar>

                  <v-list-item-content class="text-left">
                    Semestre: {{ postulacion.semestre_secretaria }}
                  </v-list-item-content>
                </v-list-item>

                <v-list-item>
                  <v-list-item-avatar>
                    <v-icon class="grey darken-3" dark>
                      fas fa-file-alt
                    </v-icon>
                  </v-list-item-avatar>

                  <v-list-item-content class="text-left">
                    Sexo: {{ postulacion.sexo_secretaria }}
                  </v-list-item-content>
