                    </v-icon>
                  </v-list-item-avatar>

                  <v-list-item-content class="text-left">
                    Género: {{ postulacion.genero_secretaria }}
                  </v-list-item-content>
                </v-list-item>

                <v-list-item>
                  <v-list-item-avatar>
                    <v-icon class="grey darken-3" dark>
                      fas fa-file-alt
                    </v-icon>
                  </v-list-item-avatar>

                  <v-list-item-content class="text-left">
                    Código U de G: {{ postulacion.codigo_secretaria }}
