            <v-btn text @click="modal = false">CERRAR</v-btn>
          </v-card-actions>
        </v-card>
      </template></v-dialog
    >
    <!-- Dialogo para exportar los datos -->
    <v-dialog v-model="dialog_export" width="500" persistent>
      <v-card>
        <v-card-title class="text-h5 primary white--text">
          <v-icon color="white" class="mr-2">fas fa-file-export</v-icon>
          Exportar datos
        </v-card-title>

        <v-card-text>
          <v-radio-group v-model="tipo_datos_exportar">
            <template v-slot:label>
              <div>
                <strong class="text-h6">Seleccione los datos a exportar</strong>
              </div>
            </template>
            <v-radio value="Por aprobar">
              <template v-slot:label>
                <div><strong class="secundary--text">Por aprobar</strong></div>
              </template>
            </v-radio>
            <v-radio value="Aprobado">
              <template v-slot:label>
                <div><strong class="success--text">Aprobados</strong></div>
              </template>
            </v-radio>
            <v-radio value="Rechazado">
              <template v-slot:label>
                <div><strong class="error--text">Rechazados</strong></div>
              </template>
            </v-radio>
          </v-radio-group>
          <v-btn
            class="ma-2"
            large
            :loading="loading_export"
            :disabled="loading_export"
            color="info"
            @click="exportDataAprobadoRechazado"
          >
            Exportar
            <template v-slot:loader>
              <span>Obteniendo...</span>
            </template>
          </v-btn>
          <v-btn
            v-if="file_existe"
            class="ma-2"
            large
            color="success"
            :href="`data:attachment/csv,${file_export}`"
            :download="`${tipo_datos_exportar}_exportado.csv`"
          >
            Descargar
          </v-btn>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="
              (dialog_export = false),
                (tipo_datos_exportar = null),
                (file_existe = null)
            "
          >
            Cerrar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Dialogo para eliminar Folio -->
    <v-dialog v-model="dialog_eliminar" width="500" persistent>
      <v-card>
        <v-card-title class="text-h5 primary white--text">
          <v-icon color="white" class="mr-2">fas fa-trash</v-icon>Eliminar
        </v-card-title>

        <v-card-text>
          <p class="font-weight-medium">
            ¿Desea eliminar toda la información relacionada al folio:
            {{ folio_eliminar }}?
          </p>
          <p class="font-weight-medium red--text">
            Esta acción se podrá deshacer.
          </p>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="secondary" text @click="dialog_eliminar = false">
            Cancelar
          </v-btn>
          <v-btn color="error" text @click="deleteFolio(folio_eliminar)">
            Confirmar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { mapMutations } from "vuex";
import axios from "axios";
// import { Base64 } from "js-base64";
import { ExportToCsv } from "export-to-csv";
export default {
  name: "Busqueda",
  data: () => ({
    user: {},
    motivo: null,
    modal: false,
    dialog_export: false,
    vicepresidencia_ejecutiva_visible: false,
    loading: false,
    buscar: null,
    eleccion_id: null,
    niveles: [],
    grupos_politicos: [],
    centros: [],
    divisiones: [],
    turnos: [],
    folio: null,
    nombre: null,
    nivel: null,
    centro: null,
    division: null,
    turno: null,
    postulacion: [],
    password: "",
    passwordRules: [
      v => !!v || "Contraseña es requerida",
      v => (v && v.length >= 8) || "Minímo 8 caracteres"
    ],
    email: "",
    emailRules: [
      v => !!v || "Correo electrónico es requerido",
      v => /.+@.+\..+/.test(v) || "Correo electrónico debe ser válido"
    ],
    resultados: [
      /*{
            folio: 1234567,
            presidencia: 'Pablo Peréz',
            grupopolitico: 'INDIGO',
            plantel: 'Preparatoria N° 1',
            turno: 'Matutino',
            estatus: 'ACEPTADO'
          },
          {
            folio: 7654321,
            presidencia: 'Pablo Peréz',
            grupopolitico: 'INDIGO',
            plantel: 'Preparatoria N° 1',
            turno: 'Matutino',
            estatus: 'RECHAZADO'
          },*/
    ],
    tipo_datos_exportar: null,
    loading_export: false,
    file_existe: null,
    file_export: null,
    dialog_eliminar: false,
    folio_eliminar: null
  }),
  mounted() {
    let existe = this.$session.exists("token");
    if (!existe) {
      this.$router.push({ name: "IndexAdmin" });
    }
    this.user = this.$session.get("user");
    this.getEleccionId();
    this.getObtenerNiveles();
    this.getTurnos();
    this.getGruposPoliticos();
  },
  methods: {
    ...mapMutations(["ocultarLoad", "mostrarLoad"]),
    async getEleccionId() {
      let id_eleccion = await axios.get(
        "https://feuapi.herokuapp.com/obtainactiveelection"
      );
      //console.log(id_eleccion.data.data[0].eleccion_id)
      this.eleccion_id = id_eleccion.data.data[0].eleccion_id;
    },
    async getGruposPoliticos() {
      let grupospoliticos = await axios.get(
        "https://feuapi.herokuapp.com/obtainpoliticalgroups"
      );
      this.grupos_politicos = grupospoliticos.data.data;
    },
    async getObtenerNiveles() {
      this.loading = true;
      let niveles = await axios.get(
        "https://feuapi.herokuapp.com/obtainnivels"
      );
      this.loading = false;
      //console.log(`Niveles: ${niveles.data} `)
      let lista_niveles = niveles.data.data;
      Object.keys(lista_niveles).forEach(element => {
        this.niveles.push({
          value: lista_niveles[element].nivel,
          label: lista_niveles[element].nivel
        });
      });
    },
    async postObtenerCentrosUniversitarios() {
      this.loading = true;
      this.divisiones = [];
      this.centros = [];
      this.division = null;
      this.centro = null
      this.turno = null
      let respuesta = await axios.post(
        "https://feuapi.herokuapp.com/obtaincentros",
        {
          eleccion_id: this.eleccion_id
        }
      );
      this.loading = false;
      let lista_centros = respuesta.data.data;
      Object.keys(lista_centros).forEach(element => {
        //console.log( lista_niveles[element].nivel);
        this.centros.push({
          value: lista_centros[element].centro,
          label: lista_centros[element].centro_desc
        });
      });
      //Se comprueba si es preparatoria
      if (this.nivel === "Preparatoria") {
        this.centros = this.centros.filter(function(centro) {
          return centro.value === "SEMS";
        });
      } else {
        this.centros = this.centros.filter(function(centro) {
          return centro.value !== "SEMS";
        });
      }
    },
    async postObtenerDivisiones() {
      this.loading = true;
      this.divisiones = [];
      let respuesta = await axios.post(
        "https://feuapi.herokuapp.com/obtaindivision",
        {
          eleccion_id: this.eleccion_id,
          centro: this.centro
        }
      );
      this.loading = false;
      let lista_divisiones = respuesta.data.data;
      Object.keys(lista_divisiones).forEach(element => {
        //console.log( lista_niveles[element].nivel);
        this.divisiones.push({
          value: lista_divisiones[element].plantel,
          label: lista_divisiones[element].plantel
        });
      });
    },
    async getTurnos() {
      this.loading = true;
      let turno = await axios.get("https://feuapi.herokuapp.com/obtainturns");
      this.loading = false;
      let tn = turno.data.data;
      Object.keys(tn).forEach(element => {
        //console.log( gn[element].genero);
        this.turnos.push(tn[element].turno);
      });
    },
    //Modal para visualizar los datos
    async modalVisualizacion(item, estatus) {
      this.mostrarLoad({ titulo: "Por favor espere" });
      let resultados = await axios.get(
        "https://feuapi.herokuapp.com/getAllData",
        {
          params: {
            folio: item
          }
        }
      );
      this.ocultarLoad();
      console.log(resultados.data.data[0][0]);
      this.postulacion = resultados.data.data[0][0];
      this.postulacion.estatus = estatus;
      if (
        this.postulacion.corriente_politica_presidente &&
        this.postulacion.grupo_politico_vicepresidencia
      ) {
        this.postulacion.corriente_politica_presidente = this.buscarNombreGrupoPolitico(
          this.postulacion.corriente_politica_presidente
        );
        this.postulacion.grupo_politico_vicepresidencia = this.buscarNombreGrupoPolitico(
          this.postulacion.grupo_politico_vicepresidencia
        );
        if (this.postulacion.vicepresidencia_ejecutiva[0].codigo) {
          this.vicepresidencia_ejecutiva_visible = true;
        } else {
          this.vicepresidencia_ejecutiva_visible = false;
        }
        this.modal = true;
      } else {
        this.$toast.open({
          message: "¡Disculpe el registro no esta completo!",
          type: "error",
          position: "bottom-right",
          duration: 5000
        });
      }
    },
    async getBuscar() {
      this.mostrarLoad({ titulo: "Por favor espere" });
      this.resultados = [];
      let nivel_id;
      if (this.nivel === "Preparatoria") {
        nivel_id = 1;
      } else if(this.nivel === "Universidad") {
        nivel_id = 2;
      } else {
        nivel_id = null
      }

      if (this.folio === '') {
        this.folio = null
      }
      try {
      let resultados = await axios.get(
        "https://feuapi.herokuapp.com/getRegister",
        {
          params: {
            folio: this.folio,
            nombre: this.nombre,
            nivel: nivel_id,
            centro_presidencia: this.centro,
            division_presidencia: this.division,
            turno_presidente: this.turno
          }
        }
      );
      this.ocultarLoad();
      //console.log(resultados.data.data[0]);
      if (resultados.data.data[0].length !== 0) {
        this.resultados = resultados.data.data[0];
      } else {
        this.$toast.open({
          message: "¡No se encontraron resultados!",
          type: "error",
          position: "bottom-right",
          duration: 5000
        });
      }
      } catch (ex) {
          console.error("inner", ex.message);
          this.ocultarLoad();
          this.$toast.open({
          message: '¡No se pudo procesar la información, por favor intentelo más tarde!',
          type: "error",
          position: "bottom-right",
          duration: 5000,
        });
        } finally {
          this.ocultarLoad();
          console.log("finally");
        }
    },
    buscarNombreGrupoPolitico(id) {
      if (id) {
        let grupo_politico = this.grupos_politicos.find(
          grupo => grupo.id === id
        );
        return grupo_politico.nombre;
      } else {
        return "";
      }
    },
    buscarNombreCentro(centro) {
      if (centro) {
        let nombre_centro = this.centros.find(
          centrof => centrof.value === centro
        );
        return nombre_centro.label;
      } else {
        return "";
      }
    },
    async updateEstatus(estatus) {
      if (estatus !== "Rechazado") {
        this.mostrarLoad({ titulo: "Por favor espere" });
        await axios.post("https://feuapi.herokuapp.com/changeEstatus", {
          folio: this.postulacion.folio,
          estatus: estatus,
          motivo: this.motivo
        });
        this.ocultarLoad();
        this.postulacion.estatus = estatus;
        this.getBuscar();
      } else {
        if (this.motivo) {
          this.mostrarLoad({ titulo: "Por favor espere" });
          await axios.post("https://feuapi.herokuapp.com/changeEstatus", {
            folio: this.postulacion.folio,
            estatus: estatus,
            motivo: this.motivo
          });
          this.ocultarLoad();
          this.postulacion.estatus = estatus;
          this.getBuscar();
        } else {
          this.$toast.open({
            message: "¡Disculpe debe indicar el motivo!",
            type: "error",
            position: "bottom-right",
            duration: 5000
          });
        }
      }
    },
    async exportDataAprobadoRechazado() {
      if (this.tipo_datos_exportar) {
        this.loading_export = true;
        try {
        let resultados = await axios.get(
          "https://feuapi.herokuapp.com/exportAcepted",
          {
            params: {
              estatus: this.tipo_datos_exportar
            }
          }
        );
        this.loading_export = false;
        this.file_existe = resultados.data.data;
       /* Base64.extendString();
        this.file_export = Base64.decode(resultados.data.data); */
        const options = {
          fieldSeparator: ",",
          quoteStrings: '"',
          decimalSeparator: ".",
          showLabels: true,
          showTitle: true,
          title: "My Awesome CSV",
          useTextFile: false,
          useBom: true,
          useKeysAsHeaders: true
          // headers: ['Column 1', 'Column 2', etc...] <-- Won't work with useKeysAsHeaders present!
        };

        const csvExporter = new ExportToCsv(options);

        await csvExporter.generateCsv(this.file_existe);
        } catch (ex) {
          console.error("inner", ex.message);
          this.ocultarLoad();
          this.$toast.open({
          message: '¡No se pudo procesar la información, por favor intentelo más tarde!',
          type: "error",
          position: "bottom-right",
          duration: 5000,
        });
        } finally {
          this.ocultarLoad();
          console.log("finally");
        }
        //
      } else {
        this.$toast.open({
          message: "¡Disculpe debe seleccionar una opción!",
          type: "error",
          position: "bottom-right",
          duration: 5000
        });
      }

      //
    },
    modalEliminar(item) {
      this.dialog_eliminar = true;
      this.folio_eliminar = item.folio;
    },
    async deleteFolio(folio) {
      this.mostrarLoad({ titulo: "Por favor espere" });
      let resultados = await axios.delete(
        "https://feuapi.herokuapp.com/deleteRegister",
        {
          data: { folio: folio }
        }
      );
      if (resultados.data.data === "Registro eliminado") {
        this.getBuscar();
        this.ocultarLoad();
        this.dialog_eliminar = false;
        this.$toast.open({
          message: "¡Registro eliminado con éxito!",
          type: "success",
          position: "bottom-right",
          duration: 5000
        });
      } else {
        this.ocultarLoad();
        this.$toast.open({
          message: "¡Disculpe ocurrio un error!",
          type: "error",
          position: "bottom-right",
          duration: 5000
        });
      }
    }
  }
};
</script>