import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Noactiva from '../views/Noactiva.vue'
import Registro from '../views/Registro.vue'
import Registro02 from '../views/Registro2.vue'
import IndexAdmin from '../views/admin/Index.vue'
import Busqueda from '../views/admin/Busqueda.vue'

Vue.use(VueRouter)

const routes = [
{
    path: '/',
    name: 'Noactiva',
    component: Noactiva
  },
  {
    path: '/registro',
    name: 'Registro',
    component: Registro
  },
  {
    path: '/registro02/:eleccion_id/:codigo/:nivel/:id_folio',
    name: 'Registro02',
    component: Registro02,
    props: true
  },
  {
    path: '/registro_admin',
    name: 'IndexAdmin',
    component: IndexAdmin
  },
  {
      path: '/busqueda',
      name: 'Busqueda',
      component: Busqueda
    },
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
